.desc {
  text-align: center;
  position: absolute;
  font-family: "Gotu", sans-serif;
  /* font-family: 'Caveat', cursive; */
  background: rgba(100%, 100%, 100%, 0.5);
  font-size: 30px;
  top: 69%;
  padding: 20px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.avatar {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.insideName {
  font-weight: bold;
  color: black;
  position: absolute;
  top: 49%;
  left: 50%;
  font-size: 50px;
  transform: translate(-50%, -50%);
}

.name {
  height: 80vh;
  font-family: "Roboto", sans-serif;
}

.jumbotron {
  opacity: 0.6;
}

.cardWrapper {
  padding: 2%;
}

.cards {
  padding: 5%;
}

.card {
  width: 520px;
}

.top {
  opacity: 0.7;
  transition: 0.6s;
}

.top:hover {
  opacity: 1;
}

.logos {
  position: absolute;
  top: 85%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.logos > a > img {
  margin: 50px;
  width: 40px;
  transition: 0.6s;
}

.logos > a > img:hover {
  opacity: 0.7;
}

@media (max-width: 1250px) {
  .card {
    width: 415px;
  }
}

@media (max-width: 990px) {
  .insideName {
      font-size: 45px;
  }
  
  .desc {
  
    font-size: 20px;
  }
  .card {
    width: 305px;
  }

  .logos > a > img {
    margin: 20px;
  }
}

@media (max-width: 750px) {

  .insideName {
    font-size: 21px;
  }

  .desc {
  
    font-size: 10px;
  }

  .card {
    width: 100%;
  }
  .logos > a > img {
    margin: 10px;
  }
}

